@import url("https://fonts.googleapis.com/css2?family=Spectral:wght@300&display=swap");

body {
  margin: 0;
  font-family: "Spectral", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  text-align: center;
  background-color: #f0fff5;
}
h1,
h3 {
  color: #45a590;
}
h4 {
  color: #992d23;
}
.nav {
  background-color: #49a30d;
  background-image: url("https://www.transparenttextures.com/patterns/worn-dots.png");
  color: white;
  text-align: center;
  padding: 4px;
}

ul {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  text-decoration: none;
}
a {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
}
.btn-up {
  background-color: #f0fff5;
  border: none;
  border-radius: 10px;
  padding-right: 20px;
  padding-top: 30px;
}
.btn-down {
  background-color: #f0fff5;
  border: none;
  padding-left: 20px;
  padding-top: 30px;
}

.btn-up svg {
  fill: #45a590;
  height: 2em;
  width: 2em;
}
.btn-down svg {
  fill: red;
  border: none;
  height: 2em;
  width: 2em;
}
.arrow {
  color: #45a590;
  padding: 20px;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #f0fff5;
  color: #45a590;
  text-align: center;
}

.displayCard {
  display: inline-block;
  padding: 5px;
  margin: 10px;
}
.displayCard img {
  border-radius: 10%;
  max-height: 130px;
}
